export default {
  tagName: "el-checkbox",
  desc: "el-checkbox",
  props: [
    { name: "value", desc: "", options: [], value: "" },
    {
      name: "label",
      desc: "checkbox-grouparray",
      options: [],
      value: ""
    },
    { name: "true-label", desc: "", options: [], value: "" },
    { name: "false-label", desc: "", options: [], value: "" },
    {
      name: "disabled",
      desc: "",
      type: "Boolean",
      options: [],
      value: "false"
    },
    {
      name: "border",
      desc: "",
      type: "Boolean",
      options: [],
      value: "false"
    },
    {
      name: "size",
      desc: "Checkbox  border ",
      type: "String",
      options: ["medium", "small", "mini"],
      value: ""
    },
    {
      name: "name",
      desc: " name ",
      type: "String",
      options: [],
      value: ""
    },
    {
      name: "checked",
      desc: "",
      type: "Boolean",
      options: [],
      value: "false"
    },
    {
      name: "indeterminate",
      desc: " indeterminate ",
      type: "Boolean",
      options: [],
      value: "false"
    }
  ]
};



