export default {
  tagName: "el-form-item",
  desc: "el-form-item",
  props: [
    {
      name: "prop",
      desc:
        " model  validateresetFields ",
      type: "String",
      options: [" Form  model "],
      value: ""
    },
    { name: "label", desc: "", type: "String", options: [], value: "" },
    {
      name: "label-width",
      desc: " '50px' auto",
      type: "String",
      options: [],
      value: ""
    },
    {
      name: "required",
      desc: "",
      type: "Boolean",
      options: [],
      value: "false"
    },
    {
      name: "rules",
      desc: "",
      type: "object",
      options: [],
      value: ""
    },
    {
      name: "error",
      desc:
        ", error",
      type: "String",
      options: [],
      value: ""
    },
    {
      name: "show-message",
      desc: "",
      type: "Boolean",
      options: [],
      value: "true"
    },
    {
      name: "inline-message",
      desc: "",
      type: "Boolean",
      options: [],
      value: "false"
    },
    {
      name: "size",
      desc: "",
      type: "String",
      options: ["medium", "small", "mini"],
      value: ""
    }
  ]
};



