export default {
  tagName: "el-checkbox-button",
  desc: "el-checkbox-button",
  props: [
    {
      name: "label",
      desc: "checkbox-grouparray",
      options: [],
      value: ""
    },
    { name: "true-label", desc: "", options: [], value: "" },
    { name: "false-label", desc: "", options: [], value: "" },
    {
      name: "disabled",
      desc: "",
      type: "Boolean",
      options: [],
      value: "false"
    },
    {
      name: "name",
      desc: " name ",
      type: "String",
      options: [],
      value: ""
    },
    {
      name: "checked",
      desc: "",
      type: "Boolean",
      options: [],
      value: "false"
    }
  ]
};



