export default {
  tagName: "el-link",
  desc: "Link ",
  props: [
    {
      name: "type",
      desc: "",
      type: "String",
      options: ["primary", "success", "warning", "danger", "info"],
      value: "default"
    },
    {
      name: "underline",
      desc: "",
      type: "Boolean",
      options: [],
      value: "true"
    },
    {
      name: "disabled",
      desc: "",
      type: "Boolean",
      options: [],
      value: "false"
    },
    {
      name: "href",
      desc: " href ",
      type: "String",
      options: [],
      value: ""
    },
    {
      name: "icon",
      desc: "",
      type: "String",
      options: [],
      value: "",
      isIconNamePick: true
    }
  ]
};



