export default {
  tagName: "el-time-picker",
  desc: "el-time-picker",
  props: [
    {
      name: "value",
      desc: "",
      type: "date(TimePicker) / string(TimeSelect)",
      options: [],
      value: ""
    },
    {
      name: "readonly",
      desc: "",
      type: "Boolean",
      options: [],
      value: "false"
    },
    {
      name: "disabled",
      desc: "",
      type: "Boolean",
      options: [],
      value: "false"
    },
    {
      name: "editable",
      desc: "",
      type: "Boolean",
      options: [],
      value: "true"
    },
    {
      name: "clearable",
      desc: "",
      type: "Boolean",
      options: [],
      value: "true"
    },
    {
      name: "size",
      desc: "",
      type: "String",
      options: ["medium", "small", "mini"],
      value: ""
    },
    {
      name: "placeholder",
      desc: "",
      type: "String",
      options: [],
      value: ""
    },
    {
      name: "start-placeholder",
      desc: "",
      type: "String",
      options: [],
      value: ""
    },
    {
      name: "end-placeholder",
      desc: "",
      type: "String",
      options: [],
      value: ""
    },
    {
      name: "is-range",
      desc: "<el-time-picker>",
      type: "Boolean",
      options: [],
      value: "false"
    },
    {
      name: "arrow-control",
      desc: "<el-time-picker>",
      type: "Boolean",
      options: [],
      value: "false"
    },
    {
      name: "align",
      desc: "",
      type: "String",
      options: ["left", "center", "right"],
      value: "left"
    },
    {
      name: "popper-class",
      desc: "TimePicker ",
      type: "String",
      options: [],
      value: ""
    },
    {
      name: "picker-options",
      desc: "",
      type: "object",
      options: [],
      value: "{}"
    },
    {
      name: "range-separator",
      desc: "",
      type: "String",
      options: ["-"],
      value: "'-'"
    },
    {
      name: "value-format",
      desc:
        "TimePicker Date ",
      type: "String",
      options: [""],
      value: ""
    },
    {
      name: "default-value",
      desc: "",
      type: "Date(TimePicker) / string(TimeSelect)",
      options: ["new Date()(TimePicker)", "(TimeSelect)"],
      value: ""
    },
    { name: "name", desc: "", type: "String", options: [], value: "" },
    {
      name: "prefix-icon",
      desc: "",
      type: "String",
      options: [],
      value: "el-icon-time"
    },
    {
      name: "clear-icon",
      desc: "",
      type: "String",
      options: [],
      value: "el-icon-circle-close"
    }
  ]
};



