export default {
  tagName: "el-transfer",
  desc: "el-transfer",
  props: [
    {
      name: "value",
      desc: "",
      type: "array",
      options: [],
      value: ""
    },
    {
      name: "data",
      desc: "Transfer ",
      type: "array[{ key, label, disabled }]",
      options: [],
      value: "[ ]"
    },
    {
      name: "filterable",
      desc: "",
      type: "Boolean",
      options: [],
      value: "false"
    },
    {
      name: "filter-placeholder",
      desc: "",
      type: "String",
      options: [],
      value: ""
    },
    {
      name: "filter-method",
      desc: "",
      type: "function",
      options: [],
      value: ""
    },
    {
      name: "target-order",
      desc:
        " original push unshift",
      type: "String",
      options: ["original", "push", "unshift"],
      value: "original"
    },
    {
      name: "titles",
      desc: "",
      type: "array",
      options: [],
      value: "[' 1', ' 2']"
    },
    {
      name: "button-texts",
      desc: "",
      type: "array",
      options: [],
      value: "[ ]"
    },
    {
      name: "render-content",
      desc: "",
      type: "function(h, option)",
      options: [],
      value: ""
    },
    {
      name: "format",
      desc: "",
      type: "object{noChecked, hasChecked}",
      options: [],
      value:
        "{ noChecked: '${checked}/${total}', hasChecked: '${checked}/${total}' }"
    },
    {
      name: "props",
      desc: "",
      type: "object{key, label, disabled}",
      options: [],
      value: ""
    },
    {
      name: "left-default-checked",
      desc: " key ",
      type: "array",
      options: [],
      value: "[ ]"
    },
    {
      name: "right-default-checked",
      desc: " key ",
      type: "array",
      options: [],
      value: "[ ]"
    }
  ]
};



