export default {
  tagName: "el-color-picker",
  desc: "el-color-picker",
  props: [
    {
      name: "value",
      desc: "",
      type: "String",
      options: [],
      value: ""
    },
    {
      name: "disabled",
      desc: "",
      type: "Boolean",
      options: [],
      value: "false"
    },
    {
      name: "size",
      desc: "",
      type: "String",
      options: [],
      value: "medium / small / mini"
    },
    {
      name: "show-alpha",
      desc: "",
      type: "Boolean",
      options: [],
      value: "false"
    },
    {
      name: "color-format",
      desc: " v-model ",
      type: "String",
      options: ["hsl", "hsv", "hex", "rgb"],
      value: "hexshow-alpha  false/ rgbshow-alpha  true"
    },
    {
      name: "popper-class",
      desc: "ColorPicker ",
      type: "String",
      options: [],
      value: ""
    },
    {
      name: "predefine",
      desc: "",
      type: "array",
      options: [],
      value: ""
    }
  ]
};



