export default {
  tagName: "el-button",
  desc: "button",
  props: [
    {
      name: "size",
      desc: "",
      type: "String",
      options: ["medium", "small", "mini"],
      value: ""
    },
    {
      name: "type",
      desc: "",
      type: "String",
      options: ["primary", "success", "warning", "danger", "info", "text"],
      value: ""
    },
    {
      name: "plain",
      desc: "",
      type: "Boolean",
      options: [],
      value: "false"
    },
    {
      name: "round",
      desc: "",
      type: "Boolean",
      options: [],
      value: "false"
    },
    {
      name: "circle",
      desc: "",
      type: "Boolean",
      options: [],
      value: "false"
    },
    {
      name: "loading",
      desc: "",
      type: "Boolean",
      options: [],
      value: "false"
    },
    {
      name: "disabled",
      desc: "",
      type: "Boolean",
      options: [],
      value: "false"
    },
    {
      name: "icon",
      desc: "",
      type: "String",
      options: [],
      isIconNamePick: true,
      value: ""
    },
    {
      name: "autofocus",
      desc: "",
      type: "Boolean",
      options: [],
      value: "false"
    },
    {
      name: "native-type",
      desc: " type ",
      type: "String",
      options: ["button", "submit", "reset"],
      value: "button"
    }
  ]
};



