export default {
  tagName: "el-checkbox-group",
  desc: "el-checkbox-group",
  props: [
    { name: "value", desc: "", options: [], value: "" },
    {
      name: "size",
      desc: " Checkbox  Checkbox ",
      type: "String",
      options: ["medium", "small", "mini"],
      value: ""
    },
    {
      name: "disabled",
      desc: "",
      type: "Boolean",
      options: [],
      value: "false"
    },
    {
      name: "min",
      desc: " checkbox ",
      options: [],
      type: "Number",
      value: ""
    },
    {
      name: "max",
      desc: " checkbox ",
      options: [],
      type: "Number",
      value: ""
    },
    {
      name: "text-color",
      desc: " Checkbox ",
      type: "String",
      options: [],
      value: "#ffffff"
    },
    {
      name: "fill",
      desc: " Checkbox ",
      type: "String",
      options: [],
      value: "#409EFF"
    }
  ]
};



