export default {
  tagName: "el-input-number",
  desc: "el-input-number",
  props: [
    {
      name: "value",
      desc: "",
      type: "Number",
      options: [],
      value: "0"
    },
    {
      name: "min",
      desc: "",
      type: "Number",
      options: [],
      value: ""
    },
    {
      name: "max",
      desc: "",
      type: "Number",
      options: [],
      value: ""
    },
    {
      name: "step",
      desc: "",
      type: "Number",
      options: [],
      value: "1"
    },
    {
      name: "step-strictly",
      desc: " step ",
      type: "Number",
      options: [],
      value: ""
    },
    {
      name: "precision",
      desc: "",
      type: "Number",
      options: [],
      value: ""
    },
    {
      name: "size",
      desc: "",
      type: "String",
      options: ["large", "small"],
      value: ""
    },
    {
      name: "disabled",
      desc: "",
      type: "Boolean",
      options: [],
      value: "false"
    },
    {
      name: "controls",
      desc: "",
      type: "Boolean",
      options: [],
      value: "true"
    },
    {
      name: "controls-position",
      desc: "",
      type: "String",
      options: ["right"],
      value: ""
    },
    { name: "name", desc: "", type: "String", options: [], value: "" },
    {
      name: "label",
      desc: "label",
      type: "String",
      options: [],
      value: ""
    },
    {
      name: "placeholder",
      desc: " placeholder",
      type: "String",
      options: [],
      value: ""
    }
  ]
};



