export default {
  tagName: "el-date-picker",
  desc: "el-date-picker",
  props: [
    {
      name: "value",
      desc: "",
      type: "date(DatePicker) / array(DateRangePicker)",
      options: [],
      value: ""
    },
    {
      name: "readonly",
      desc: "",
      type: "Boolean",
      options: [],
      value: "false"
    },
    {
      name: "disabled",
      desc: "",
      type: "Boolean",
      options: [],
      value: "false"
    },
    {
      name: "editable",
      desc: "",
      type: "Boolean",
      options: [],
      value: "true"
    },
    {
      name: "clearable",
      desc: "",
      type: "Boolean",
      options: [],
      value: "true"
    },
    {
      name: "size",
      desc: "",
      type: "String",
      options: ["large, small, mini"],
      value: ""
    },
    {
      name: "placeholder",
      desc: "",
      type: "String",
      options: [],
      value: ""
    },
    {
      name: "start-placeholder",
      desc: "",
      type: "String",
      options: [],
      value: ""
    },
    {
      name: "end-placeholder",
      desc: "",
      type: "String",
      options: [],
      value: ""
    },
    {
      name: "type",
      desc: "",
      type: "String",
      options: [
        "year",
        "month",
        "date",
        "dates",
        " week",
        "datetime",
        "datetimerange",
        " daterange",
        "monthrange"
      ],
      value: "date"
    },
    {
      name: "format",
      desc: "",
      type: "String",
      options: [""],
      value: "yyyy-MM-dd"
    },
    {
      name: "align",
      desc: "",
      type: "String",
      options: ["left, center, right"],
      value: "left"
    },
    {
      name: "popper-class",
      desc: "DatePicker ",
      type: "String",
      options: [],
      value: ""
    },
    {
      name: "picker-options",
      desc: "",
      type: "object",
      options: [],
      value: "{}"
    },
    {
      name: "range-separator",
      desc: "",
      type: "String",
      options: [],
      value: "'-'"
    },
    {
      name: "default-value",
      desc: "",
      type: "Date",
      options: ["new Date()"],
      value: ""
    },
    {
      name: "default-time",
      desc: "",
      type: "string[]",
      options: [
        " 212:00:00 00:00:00"
      ],
      value: ""
    },
    {
      name: "value-format",
      desc: " Date ",
      type: "String",
      options: [""],
      value: ""
    },
    { name: "name", desc: "", type: "String", options: [], value: "" },
    {
      name: "unlink-panels",
      desc: "",
      type: "Boolean",
      options: [],
      value: "false"
    },
    {
      name: "prefix-icon",
      desc: "",
      type: "String",
      options: [],
      value: "el-icon-date"
    },
    {
      name: "clear-icon",
      desc: "",
      type: "String",
      options: [],
      value: "el-icon-circle-close"
    },
    {
      name: "validate-event",
      desc: "",
      type: "Boolean",
      options: ["-"],
      value: "true"
    }
  ]
};



