export default {
  tagName: "el-slider",
  desc: "el-slider",
  props: [
    {
      name: "value / v-model",
      desc: "",
      type: "Number",
      options: [],
      value: "0"
    },
    { name: "min", desc: "", type: "Number", options: [], value: "0" },
    { name: "max", desc: "", type: "Number", options: [], value: "100" },
    {
      name: "disabled",
      desc: "",
      type: "Boolean",
      options: [],
      value: "false"
    },
    { name: "step", desc: "", type: "Number", options: [], value: "1" },
    {
      name: "show-input",
      desc: "",
      type: "Boolean",
      options: [],
      value: "false"
    },
    {
      name: "show-input-controls",
      desc: "",
      type: "Boolean",
      options: [],
      value: "true"
    },
    {
      name: "input-size",
      desc: "",
      type: "String",
      options: ["large", "medium", "small", "mini"],
      value: "small"
    },
    {
      name: "show-stops",
      desc: "",
      type: "Boolean",
      options: [],
      value: "false"
    },
    {
      name: "show-tooltip",
      desc: " tooltip",
      type: "Boolean",
      options: [],
      value: "true"
    },
    {
      name: "format-tooltip",
      desc: " tooltip message",
      type: "function(value)",
      options: [],
      value: ""
    },
    {
      name: "range",
      desc: "",
      type: "Boolean",
      options: [],
      value: "false"
    },
    {
      name: "vertical",
      desc: "",
      type: "Boolean",
      options: [],
      value: "false"
    },
    {
      name: "height",
      desc: "Slider ",
      type: "String",
      options: [],
      value: ""
    },
    {
      name: "label",
      desc: "",
      type: "String",
      options: [],
      value: ""
    },
    {
      name: "debounce",
      desc: "show-inputtrue",
      type: "Number",
      options: [],
      value: "300"
    },
    {
      name: "tooltip-class",
      desc: "tooltip ",
      type: "String",
      options: [],
      value: ""
    },
    {
      name: "marks",
      desc:
        " key  number  [min, max] ",
      type: "object",
      options: [],
      value: ""
    }
  ]
};



