export default {
  tagName: "el-radio-group",
  desc: "el-radio-group",
  props: [
    { name: "value", desc: "", options: [], value: "" },
    {
      name: "size",
      desc: " Radio  Radio ",
      type: "String",
      options: ["medium", "small", "mini"],
      value: ""
    },
    {
      name: "disabled",
      desc: "",
      type: "Boolean",
      options: [],
      value: "false"
    },
    {
      name: "text-color",
      desc: " Radio ",
      type: "String",
      options: [],
      value: "#ffffff"
    },
    {
      name: "fill",
      desc: " Radio ",
      type: "String",
      options: [],
      value: "#409EFF"
    }
  ]
};



