export default {
  tagName: "el-radio",
  desc: "Radio ",
  props: [
    { name: "value", desc: "", options: [], value: "" },
    { name: "label", desc: "Radio  value", options: [], value: "" },
    {
      name: "disabled",
      desc: "",
      type: "Boolean",
      options: [],
      value: "false"
    },
    {
      name: "border",
      desc: "",
      type: "Boolean",
      options: [],
      value: "false"
    },
    {
      name: "size",
      desc: "Radio  border ",
      type: "String",
      options: ["medium", "small", "mini"],
      value: ""
    },
    {
      name: "name",
      desc: " name ",
      type: "String",
      options: [],
      value: ""
    }
  ]
};



